<template>
    <div>
        <ReturnNav inputTxt=" " />
        <!--   -->
        <div id="profile-edit">
            <UserProfileImage
                :profileImg="userInfo.profileImage"
                id="profile-img"
            />
            <UserProfileInfo />
        </div>
    </div>
</template>

<script>
import UserApi from "@/apis/UserApi"
import ReturnNav from "@/components/user/ReturnNav.vue"
import UserProfileImage from "./UserProfileImage.vue"
import UserProfileInfo from "./UserProfileInfo.vue"

export default {
    name: "EditProfile",
    components: {
        ReturnNav,
        UserProfileImage,
        UserProfileInfo,
    },
    data() {
        return {
            userInfo: {},
        };
    },
    created() {
        const nickname = localStorage.getItem("nickname")
        UserApi.requestProfile(nickname).then((res) => {
            this.userInfo = res.data;
        })
    },
    methods: {
        getUserInfo() {
            const nickname = localStorage.getItem("nickname")
            UserApi.requestProfile(nickname).then((res) => {
                this.userInfo = res.data;
            })
        }
    },
    // Vue router   
    // router    ,    
    // , next  callback function  this    
    beforeRouteEnter(to, from, next) {
        next(vm => {
            if (from.name === 'EditProfileMImage')
                setTimeout(vm.getUserInfo(), 1000)
        })
    },
    afterEach(to, from) {
        console.log(from)
    }
}
</script>

<style scoped>
#profile-edit {
    margin: 70px 20px 0;
}

#profile-img {
    margin: auto;
}
</style>