<template>
    <div id="info-wrap">
        <!--   -->
        <div class="info-sub-wrap">
            <div class="d-flex justify-content-between">
                <p></p>
                <input
                    v-model="userInfo.nickname"
                    type="text"
                    class="profile-input"
                />
            </div>
            <hr />
            <div class="d-flex justify-content-between">
                <p></p>
                <input
                    v-model="userInfo.bio"
                    type="text"
                    class="profile-input"
                />
            </div>
            <hr />
            <div class="d-flex justify-content-between">
                <p></p>
                <input
                    v-model="userInfo.phoneNumber"
                    type="tel"
                    class="profile-input"
                />
            </div>
            <hr />
            <router-link v-bind:to="{ name: 'EditProfileMPassword' }">
                <p class="router-link"> </p>
            </router-link>
        </div>
        <hr class="thick-hr" />
        <!--   -->
        <h2> </h2>
        <div class="info-sub-wrap">
            <div class="d-flex justify-content-between">
                <span> </span>
                <div class="form-check form-switch">
                    <input class="form-check-input" type="checkbox" />
                </div>
            </div>
            <hr />
            <div class="d-flex justify-content-between">
                <span> </span>
                <div class="form-check form-switch">
                    <input class="form-check-input" type="checkbox" />
                </div>
            </div>
            <hr />
            <div class="d-flex justify-content-between">
                <span> </span>
                <div class="form-check form-switch">
                    <input class="form-check-input" type="checkbox" />
                </div>
            </div>
        </div>
        <hr class="thick-hr" />
        <!-- /  -->
        <div class="d-flex justify-content-center">
            <button v-on:click="modifyUser" class="button-modify">
                 
            </button>
        </div>
        <hr class="thick-hr" />
        <div class="d-flex justify-content-end">
            <button v-on:click="logout">
                <i class="fas fa-sign-out-alt"></i>
            </button>
            <router-link
                v-bind:to="{ name: 'EditProfileMWithdraw' }"
                id="withdraw-button"
            >
                <p class="router-link"></p>
            </router-link>
        </div>
    </div>
</template>

<script>
import UserApi from "@/apis/UserApi";

export default {
    name: "EditProfileInfo",
    data() {
        return {
            userInfo: {},
        };
    },
    methods: {
        //    
        logout: function () {
            localStorage.removeItem("jwt");
            localStorage.removeItem("nickname");
            this.$router.push({ name: "MainPage" });
            this.$store.commit("LOG_OUT_STATE");
        },
        //     
        modifyUser: function () {
            const info = {
                bio: this.userInfo.bio,
                nickname: this.userInfo.nickname,
                phoneNumber: this.userInfo.phoneNumber
            };
            UserApi.requestModifyInfo(info).then((res) => {
                console.log(res)
                // this.userInfo = res.data;
                // localStorage.removeItem('nickname')
                console.log(res.data)
                // localStorage.setItem('nickname', res.data.nickname)
                this.$router.push({ name: "Profile", params: { id: this.userInfo.nickname } });
            })
        },
    },
    created() {
        const nickname = localStorage.getItem("nickname");
        UserApi.requestProfile(nickname).then((res) => {
            this.userInfo = res.data;
        });
    },
};
</script>

<style scoped>
#info-wrap {
    font-size: 16px;
}

.info-sub-wrap {
    margin: 15px 0 0 0;
}

#info-wrap h2 {
    font-size: 20px;
    color: #9b9b9b;
}

.profile-input {
    padding: 15px;
    border: none;
    height: 30px;
    text-align: right;
}

.profile-info {
    color: #9b9b9b;
}

.change-button {
    width: 45px;
    height: 30px;
    border-radius: 12px;
    background-color: #ff993c;
    margin: 0 0 0 10px;
}

#info-wrap hr {
    color: #ff993c;
    margin: 8px;
}

.thick-hr {
    height: 3px;
}

.router-link {
    color: black;
}

.button-modify {
    width: 200px;
    height: 35px;
    border-radius: 8px;
    background-color: #ff993c;
}

#withdraw-button {
    margin: 0 0 0 10px;
}
</style>